.header {
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    background-color: #7d69ff;
    box-shadow: '0px 4px 4px rgba(0, 0, 0, 0.25)';
}

.contents {
    display: flex;
    width: 96%;
    max-width: 1100px;
    height: 100%;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}

.navigation {
    ul {
        display: flex;
        list-style: none;
    }

    button {
        background-color: #c9c0fc;
        color: black;
        font-weight: bold;
        padding: 8px 26px;
        border: none;
        border-radius: 8px;
        cursor: pointer;
        box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* 입체적인 그림자 추가 */
    }

    li+li {
        margin-left: 30px;
    }
}