.sidebar {
    background-color: #c9c0fc;
    box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25);
    width: 270px;
    height: 100vh;
    display: flex;
    flex: 1;
}
.notice{
    color:#ccc;
}
.input {
    padding: 8px;
    font-size: 16px; /* 원하는 크기로 조정 */
    font-weight: bold;
    background-color: #a89deb;
    border: 1px solid #ccc;
    margin-right: 10px;
    width: 286px;
    height : 300px;
    align-items: center;
    justify-content: center;
    margin-top: 1px;
    resize: none; /* 수직 크기 조정 허용 */
    overflow: auto;
}

.dbbutton {
    position: fixed;
    bottom: 10px;
    left: 15px;
    background-color: #958cd1;
    color: #fff;
    font-weight: bold;
    padding: 8px 26px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* 입체적인 그림자 추가 */
}

.codebutton {
    position: fixed;
    bottom: 10px;
    left: 140px;
    background-color: #958cd1;
    color: #fff;
    font-weight: bold;
    padding: 8px 26px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* 입체적인 그림자 추가 */
}
